import React from "react";

import cloud from "./assets/cloud.png";

const Queue = () => {
  const songs = [
    { title: "Shake It Off Off", artist: "Taylor Swift" },
    { title: "Bohemian Rhapsody", artist: "Queen" },
    { title: "Rolling in the Deep", artist: "Adele" },
  ];

  return (
    <div
      className="w-full bg-white px-4 py-6 rounded-xl shadow-lg border-2 border-blue-300 relative"
      style={{
        boxShadow: "6px 6px 0px rgba(147, 197, 253, 1)",
      }}
    >
      <h3 className="text-lg font-bold text-blue-700 mb-4 ml-4">Up Next</h3>
      <div className="space-y-3">
        {songs.map((song, index) => (
          <div
            key={index}
            className="flex items-center justify-between p-3 bg-gradient-to-br from-indigo-100 via-transparent to-blue-100 rounded-lg transition-shadow border border-blue-100" style={{
                boxShadow: "4px 4px 0px rgba(147, 197, 253, 0.4)",
            }}
          >
            <div className="flex items-center space-x-2 text-blue-500">
              <i className="bi bi-play-fill text-lg"></i>
              <p className="font-medium">{song.title}</p>
            </div>
            <p className="text-blue-500">{song.artist}</p>
          </div>
        ))}
      </div>

      {/* Image */}
      <img
        src={cloud}
        alt="Decoration"
        className="absolute -bottom-28 -right-20 w-60 h-60 object-contain"
      />
    </div>
  );
};

export default Queue;
